export function disposeAll(disposables: Iterable<IDisposable>) {
	const errors: any[] = [];

	for (const disposable of disposables) {
		try {
			disposable.dispose();
		} catch (e) {
			errors.push(e);
		}
	}

	if (errors.length === 1) {
		throw errors[0];
	} else if (errors.length > 1) {
		throw new AggregateError(errors, 'Encountered errors while disposing of store');
	}
}

export interface IDisposable {
	dispose(): void;
}

export abstract class Disposable {
	private a = false;

	protected b: IDisposable[] = [];

	public dispose(): any {
		if (this.a) {
			return;
		}
		this.a = true;
		disposeAll(this.b);
	}

	protected c<T extends IDisposable>(value: T): T {
		if (this.a) {
			value.dispose();
		} else {
			this.b.push(value);
		}
		return value;
	}

	protected get d() {
		return this.a;
	}
}

