import { Connection, Emitter, FileChangeType, NotebookDocuments, Position, Range, TextDocuments } from 'vscode-languageserver';
import { TextDocument } from 'vscode-languageserver-textdocument';
import * as md from 'vscode-markdown-languageservice';
import { URI } from 'vscode-uri';
import { LsConfiguration } from './config';
import * as protocol from './protocol';
import { isMarkdownFile, looksLikeMarkdownPath } from './util/file';
import { Limiter } from './util/limiter';
import { ResourceMap } from './util/resourceMap';
import { Schemes } from './util/schemes';

declare const TextDecoder: any;

class VsCodeDocument implements md.ITextDocument {

	private a?: TextDocument;
	private b?: TextDocument;

	readonly uri: string;

	constructor(uri: string, init: { inMemoryDoc: TextDocument });
	constructor(uri: string, init: { onDiskDoc: TextDocument });
	constructor(uri: string, init: { inMemoryDoc?: TextDocument; onDiskDoc?: TextDocument }) {
		this.uri = uri;
		this.a = init?.inMemoryDoc;
		this.b = init?.onDiskDoc;
	}

	get version(): number {
		return this.a?.version ?? this.b?.version ?? 0;
	}

	get lineCount(): number {
		return this.a?.lineCount ?? this.b?.lineCount ?? 0;
	}

	getText(range?: Range): string {
		if (this.a) {
			return this.a.getText(range);
		}

		if (this.b) {
			return this.b.getText(range);
		}

		throw new Error('Document has been closed');
	}

	positionAt(offset: number): Position {
		if (this.a) {
			return this.a.positionAt(offset);
		}

		if (this.b) {
			return this.b.positionAt(offset);
		}

		throw new Error('Document has been closed');
	}

	hasInMemoryDoc(): boolean {
		return !!this.a;
	}

	isDetached(): boolean {
		return !this.b && !this.a;
	}

	setInMemoryDoc(doc: TextDocument | undefined) {
		this.a = doc;
	}

	setOnDiskDoc(doc: TextDocument | undefined) {
		this.b = doc;
	}
}

export class VsCodeClientWorkspace implements md.IWorkspaceWithWatching {

	private readonly a = new Emitter<md.ITextDocument>();
	public readonly onDidCreateMarkdownDocument = this.a.event;

	private readonly b = new Emitter<md.ITextDocument>();
	public readonly onDidChangeMarkdownDocument = this.b.event;

	private readonly c = new Emitter<URI>();
	public readonly onDidDeleteMarkdownDocument = this.c.event;

	private readonly d = new ResourceMap<VsCodeDocument>();

	private readonly f = new TextDecoder('utf-8');

	private g = 0;
	private readonly h = new Map<number, {
		readonly resource: URI;
		readonly options: md.FileWatcherOptions;
		readonly onDidChange: Emitter<URI>;
		readonly onDidCreate: Emitter<URI>;
		readonly onDidDelete: Emitter<URI>;
	}>();

	constructor(
		private readonly i: Connection,
		private readonly j: LsConfiguration,
		private readonly k: TextDocuments<TextDocument>,
		private readonly l: NotebookDocuments<TextDocument>,
		private readonly m: md.ILogger,
	) {
		k.onDidOpen(e => {
			if (!this.q(e.document)) {
				return;
			}

			this.m.log(md.LogLevel.Trace, 'VsCodeClientWorkspace: TextDocument.onDidOpen', `${e.document.uri}`);

			const uri = URI.parse(e.document.uri);
			const doc = this.d.get(uri);

			if (doc) {
				// File already existed on disk
				doc.setInMemoryDoc(e.document);

				// The content visible to the language service may have changed since the in-memory doc
				// may differ from the one on-disk. To be safe we always fire a change event.
				this.b.fire(doc);
			} else {
				// We're creating the file for the first time
				const doc = new VsCodeDocument(e.document.uri, { inMemoryDoc: e.document });
				this.d.set(uri, doc);
				this.a.fire(doc);
			}
		});

		k.onDidChangeContent(e => {
			if (!this.q(e.document)) {
				return;
			}

			this.m.log(md.LogLevel.Trace, 'VsCodeClientWorkspace: TextDocument.onDidChanceContent', `${e.document.uri}`);

			const uri = URI.parse(e.document.uri);
			const entry = this.d.get(uri);
			if (entry) {
				entry.setInMemoryDoc(e.document);
				this.b.fire(entry);
			}
		});

		k.onDidClose(async e => {
			if (!this.q(e.document)) {
				return;
			}

			this.m.log(md.LogLevel.Trace, 'VsCodeClientWorkspace: TextDocument.onDidClose', `${e.document.uri}`);

			const uri = URI.parse(e.document.uri);
			const doc = this.d.get(uri);
			if (!doc) {
				// Document was never opened
				return;
			}

			doc.setInMemoryDoc(undefined);
			if (doc.isDetached()) {
				// The document has been fully closed
				this.r(uri);
				return;
			}

			// Check that if file has been deleted on disk.
			// This can happen when directories are renamed / moved. VS Code's file system watcher does not
			// notify us when this happens.
			if (!(await this.p(uri))) {
				if (this.d.get(uri) === doc && !doc.hasInMemoryDoc()) {
					this.r(uri);
					return;
				}
			}

			// The document still exists on disk
			// To be safe, tell the service that the document has changed because the
			// in-memory doc contents may be different than the disk doc contents.
			this.b.fire(doc);
		});

		i.onDidChangeWatchedFiles(async ({ changes }) => {
			for (const change of changes) {
				const resource = URI.parse(change.uri);
				this.m.log(md.LogLevel.Trace, 'VsCodeClientWorkspace: onDidChangeWatchedFiles', `${change.type}: ${resource}`);
				switch (change.type) {
					case FileChangeType.Changed: {
						const entry = this.d.get(resource);
						if (entry) {
							// Refresh the on-disk state
							const document = await this.o(resource);
							if (document) {
								this.b.fire(document);
							}
						}
						break;
					}
					case FileChangeType.Created: {
						const entry = this.d.get(resource);
						if (entry) {
							// Create or update the on-disk state
							const document = await this.o(resource);
							if (document) {
								this.a.fire(document);
							}
						}
						break;
					}
					case FileChangeType.Deleted: {
						const entry = this.d.get(resource);
						if (entry) {
							entry.setOnDiskDoc(undefined);
							if (entry.isDetached()) {
								this.r(resource);
							}
						}
						break;
					}
				}
			}
		});

		i.onRequest(protocol.fs_watcher_onChange, params => {
			this.m.log(md.LogLevel.Trace, 'VsCodeClientWorkspace: fs_watcher_onChange', `${params.kind}: ${params.uri}`);

			const watcher = this.h.get(params.id);
			if (!watcher) {
				return;
			}

			switch (params.kind) {
				case 'create': watcher.onDidCreate.fire(URI.parse(params.uri)); return;
				case 'change': watcher.onDidChange.fire(URI.parse(params.uri)); return;
				case 'delete': watcher.onDidDelete.fire(URI.parse(params.uri)); return;
			}
		});
	}

	public listen() {
		this.i.workspace.onDidChangeWorkspaceFolders(async () => {
			this.workspaceFolders = (await this.i.workspace.getWorkspaceFolders() ?? []).map(x => URI.parse(x.uri));
		});
	}

	private n: readonly URI[] = [];

	get workspaceFolders(): readonly URI[] {
		return this.n;
	}

	set workspaceFolders(value: readonly URI[]) {
		this.n = value;
	}

	async getAllMarkdownDocuments(): Promise<Iterable<md.ITextDocument>> {
		// Add opened files (such as untitled files)
		const openTextDocumentResults = this.k.all()
			.filter(doc => this.q(doc));

		const allDocs = new ResourceMap<md.ITextDocument>();
		for (const doc of openTextDocumentResults) {
			allDocs.set(URI.parse(doc.uri), doc);
		}

		// And then add files on disk
		const maxConcurrent = 20;
		const limiter = new Limiter<md.ITextDocument | undefined>(maxConcurrent);
		const resources = await this.i.sendRequest(protocol.findMarkdownFilesInWorkspace, {});
		await Promise.all(resources.map(strResource => {
			return limiter.queue(async () => {
				const resource = URI.parse(strResource);
				if (allDocs.has(resource)) {
					return;
				}

				const doc = await this.openMarkdownDocument(resource);
				if (doc) {
					allDocs.set(resource, doc);
				}
				return doc;
			});
		}));

		return allDocs.values();
	}

	hasMarkdownDocument(resource: URI): boolean {
		return !!this.k.get(resource.toString());
	}

	async openMarkdownDocument(resource: URI): Promise<md.ITextDocument | undefined> {
		const existing = this.d.get(resource);
		if (existing) {
			return existing;
		}

		const matchingDocument = this.k.get(resource.toString());
		if (matchingDocument) {
			let entry = this.d.get(resource);
			if (entry) {
				entry.setInMemoryDoc(matchingDocument);
			} else {
				entry = new VsCodeDocument(resource.toString(), { inMemoryDoc: matchingDocument });
				this.d.set(resource, entry);
			}

			return entry;
		}

		return this.o(resource);
	}

	private async o(resource: URI): Promise<md.ITextDocument | undefined> {
		if (!looksLikeMarkdownPath(this.j, resource)) {
			return undefined;
		}

		try {
			const response = await this.i.sendRequest(protocol.fs_readFile, { uri: resource.toString() });
			// TODO: LSP doesn't seem to handle Array buffers well
			const bytes = new Uint8Array(response);

			// We assume that markdown is in UTF-8
			const text = this.f.decode(bytes);
			const doc = new VsCodeDocument(resource.toString(), {
				onDiskDoc: TextDocument.create(resource.toString(), 'markdown', 0, text)
			});
			this.d.set(resource, doc);
			return doc;
		} catch (e) {
			return undefined;
		}
	}

	async stat(resource: URI): Promise<md.FileStat | undefined> {
		this.m.log(md.LogLevel.Trace, 'VsCodeClientWorkspace: stat', `${resource}`);
		if (this.d.has(resource)) {
			return { isDirectory: false };
		}
		return this.p(resource);
	}

	private async p(resource: URI): Promise<md.FileStat | undefined> {
		const uri = resource.toString();
		if (this.k.get(uri)) {
			return { isDirectory: false };
		}
		const fsResult = await this.i.sendRequest(protocol.fs_stat, { uri });
		return fsResult ?? undefined; // Force convert null to undefined
	}

	async readDirectory(resource: URI): Promise<[string, md.FileStat][]> {
		this.m.log(md.LogLevel.Trace, 'VsCodeClientWorkspace: readDir', `${resource}`);
		return this.i.sendRequest(protocol.fs_readDirectory, { uri: resource.toString() });
	}

	getContainingDocument(resource: URI): md.ContainingDocumentContext | undefined {
		if (resource.scheme === Schemes.notebookCell) {
			const nb = this.l.findNotebookDocumentForCell(resource.toString());
			if (nb) {
				return {
					uri: URI.parse(nb.uri),
					children: nb.cells.map(cell => ({ uri: URI.parse(cell.document) })),
				};
			}
		}
		return undefined;
	}

	watchFile(resource: URI, options: md.FileWatcherOptions): md.IFileSystemWatcher {
		const id = this.g++;
		this.m.log(md.LogLevel.Trace, 'VsCodeClientWorkspace: watchFile', `(${id}) ${resource}`);

		const entry = {
			resource,
			options,
			onDidCreate: new Emitter<URI>(),
			onDidChange: new Emitter<URI>(),
			onDidDelete: new Emitter<URI>(),
		};
		this.h.set(id, entry);

		this.i.sendRequest(protocol.fs_watcher_create, {
			id,
			uri: resource.toString(),
			options,
			watchParentDirs: true,
		});

		return {
			onDidCreate: entry.onDidCreate.event,
			onDidChange: entry.onDidChange.event,
			onDidDelete: entry.onDidDelete.event,
			dispose: () => {
				this.m.log(md.LogLevel.Trace, 'VsCodeClientWorkspace: disposeWatcher', `(${id}) ${resource}`);
				this.i.sendRequest(protocol.fs_watcher_delete, { id });
				this.h.delete(id);
			}
		};
	}

	private q(doc: TextDocument) {
		return isMarkdownFile(doc) && URI.parse(doc.uri).scheme !== 'vscode-bulkeditpreview';
	}

	private r(uri: URI) {
		this.m.log(md.LogLevel.Trace, 'VsCodeClientWorkspace: deleteDocument', `${uri}`);

		this.d.delete(uri);
		this.c.fire(uri);
	}
}
