interface ILimitedTaskFactory<T> {
	factory: ITask<Promise<T>>;
	c: (value: T | Promise<T>) => void;
	e: (error?: unknown) => void;
}

interface ITask<T> {
	(): T;
}

/**
 * A helper to queue N promises and run them all with a max degree of parallelism. The helper
 * ensures that at any time no more than M promises are running at the same time.
 *
 * Taken from 'src/vs/base/common/async.ts'
 */
export class Limiter<T> {

	private a = 0;
	private b: number;
	private readonly d: number;
	private readonly f: ILimitedTaskFactory<T>[];

	constructor(maxDegreeOfParalellism: number) {
		this.d = maxDegreeOfParalellism;
		this.f = [];
		this.b = 0;
	}

	get size(): number {
		return this.a;
	}

	queue(factory: ITask<Promise<T>>): Promise<T> {
		this.a++;

		return new Promise<T>((c, e) => {
			this.f.push({ factory, c, e });
			this.g();
		});
	}

	private g(): void {
		while (this.f.length && this.b < this.d) {
			const iLimitedTask = this.f.shift()!;
			this.b++;

			const promise = iLimitedTask.factory();
			promise.then(iLimitedTask.c, iLimitedTask.e);
			promise.then(() => this.h(), () => this.h());
		}
	}

	private h(): void {
		this.a--;
		this.b--;

		if (this.f.length > 0) {
			this.g();
		}
	}
}
