import { URI } from 'vscode-uri';


type ResourceToKey = (uri: URI) => string;

const defaultResourceToKey = (resource: URI): string => resource.toString();

export class ResourceMap<T> {

	private readonly a = new Map<string, { readonly uri: URI; readonly value: T }>();

	private readonly b: ResourceToKey;

	constructor(toKey: ResourceToKey = defaultResourceToKey) {
		this.b = toKey;
	}

	public set(uri: URI, value: T): this {
		this.a.set(this.b(uri), { uri, value });
		return this;
	}

	public get(resource: URI): T | undefined {
		return this.a.get(this.b(resource))?.value;
	}

	public has(resource: URI): boolean {
		return this.a.has(this.b(resource));
	}

	public get size(): number {
		return this.a.size;
	}

	public clear(): void {
		this.a.clear();
	}

	public delete(resource: URI): boolean {
		return this.a.delete(this.b(resource));
	}

	public *values(): IterableIterator<T> {
		for (const entry of this.a.values()) {
			yield entry.value;
		}
	}

	public *keys(): IterableIterator<URI> {
		for (const entry of this.a.values()) {
			yield entry.uri;
		}
	}

	public *entries(): IterableIterator<[URI, T]> {
		for (const entry of this.a.values()) {
			yield [entry.uri, entry.value];
		}
	}

	public [Symbol.iterator](): IterableIterator<[URI, T]> {
		return this.entries();
	}
}
